body {
  margin: 0;
  padding: 0;
  font-family: 'Mochiy Pop P One', sans-serif;
  background-color: #f7f7f7;
}

.todo-wrapper {
  max-width: 600px;
  margin: 0 auto;
  position: relative;
  background-color: #fff;
  box-shadow: 0 2px 4px 0 rgba(0, 0, 0, 0.2),
    0 25px 50px 0 rgba(0, 0, 0, 0.1);
}

.title {
  font-size: 80px;
  text-align: center;
  font-weight: 900;
  color: rgba(175, 47, 47, 0.15);
  margin-bottom: 20px;
}

.todo-input-wrapper {
  position: relative;
  border-bottom: 1px solid #ededed;
}

.todo-input {
  padding: 16px 16px 16px 60px;
  border: none;
  background: rgba(0, 0, 0, 0.003);
  width: 100%;
  box-sizing: border-box;
  font-size: 28px;
  box-shadow: inset 0 -2px 40px rgba(0, 0, 0, 0.03);
}

.fa-plus {
  position: absolute;
  font-size: 24px;
  top: 50%;
  transform: translateY(-50%);
  left: 20px;
}

.todo-list {
  margin: 0;
  padding: 0;
}

.todo-item {
  font-size: 24px;
  border-bottom: 1px solid #ededed;
  min-height: 60px;
  display: flex;
  align-items: center;
  list-style: none;
  padding: 0 16px 0 60px;
  cursor: pointer;
  display: flex;
  justify-content: space-between;
  align-items: center;
  position: relative;
}

.completed {
  text-decoration: line-through;
}

.completed,
.fa-check-circle {
  color: #ccc;
}

.fa-times {
  transition: all .3s ease;
  color: rgba(175, 47, 47, 0.15);
}

.mark {
  position: absolute;
  left: 20px;
}

.todo-item:hover .fa-times {
  color: rgba(175, 47, 47, 0.9);
}

.footer {
  position: relative;
  min-height: 50px;
  display: flex;
  align-items: center;
  padding: 0 6px 0 21px;
  justify-content: space-between;
}

.amount {
  font-size: 13.3333px;
}

.btn-group {
  position: relative;
  z-index: 1;
}

.filter-btn {
  margin: 3px;
  padding: 3px 7px;
  border: 1px solid transparent;
  border-radius: 3px;
  background-color: transparent;
  cursor: pointer;
}

.filter-btn:hover,
.filter-btn.active {
  border-color: rgba(175, 47, 47, 0.2);
}

.footer:before {
  content: '';
  position: absolute;
  right: 0;
  bottom: 0;
  left: 0;
  height: 50px;
  box-shadow: 0 1px 1px rgba(0, 0, 0, 0.2),
    0 8px 0 -3px #f6f6f6,
    0 9px 1px -3px rgba(0, 0, 0, 0.2),
    0 16px 0 -6px #f6f6f6,
    0 17px 2px -6px rgba(0, 0, 0, 0.2);
}
